import { request } from 'utils';

const prefix = '/api/students';

export const getStudents = dataSearch =>
  request({
    prefix,
    url: `/Student/Search`,
    params: { ...dataSearch },
    method: 'GET',
  });

export const updateStudentCode = (
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  codeLength,
  startOrder,
  prefixCode,
  suffixCode,
  withoutCode,
  token,
  ) =>
  request({
    prefix,
    url: `/Student/UpdateStudentsCode`,
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
      Authorization: `Bearer ${token}`,
    },
    data: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      classId,
      codeLength,
      startOrder,
      prefix: prefixCode,
      suffix: suffixCode,
      withoutCode,
    },
    method: 'POST',
  });

export const deleteStudentCode = (
    trainTypeId,
    facultyId,
    courseId,
    studyFieldId,
    classId,
    token,
    ) =>
    request({
      prefix,
      url: `/Student/DeleteStudentCode`,
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json',
        Authorization: `Bearer ${token}`,
      },
      data: {
        trainTypeId,
        facultyId,
        courseId,
        studyFieldId,
        classId,
      },
      method: 'PUT',
    });

export const getUser = () =>
  request({
    prefix: '/identity',
    url: `/sign-in`,
    data: { username: 'admin', password: 'admin' },
    method: 'POST',
  });




