import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col } from 'antd';
import { TableData } from 'components/common';
import { getStudents, getUser } from 'services/StudentCode';
import { getQueryParams } from 'utils/urlUtil';
import StructCodeForm from './StructCodeForm';
import FormSearch from './FormSearch';

const columns = [
  {
    title: 'Mã SV',
    dataIndex: 'code',
    key: 'code',
  },
  {
    title: 'Họ và tên',
    dataIndex: 'fullname',
    key: 'fullname',
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'dob',
    key: 'dob',
  },
  {
    title: 'Giới tính',
    dataIndex: 'genderText',
    key: 'genderText',
  },
  {
    title: 'Tên lớp',
    dataIndex: 'className',
    key: 'toScores',
  },
];

class SetStudentCodePage extends Component {
  state = {
    reload: 0,
    paramSearchClass: {},
    token: '',
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
    getUser()
    .then(res => {
      this.setState({
        token: res.data.accessToken,
      });
    })
    .catch(error => {
      console.log(error);
    });
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = () => {
    this.setState({ paramSearchClass: getQueryParams() });
  };

  handleReload = () => {
    this.setState({ reload: this.state.reload + 1 });
  };

  render() {
    const { reload, paramSearchClass, token } = this.state;
    return (
      <Card title="Lập mã sinh viên">
        <Row>
          <Col span={24}>
            <FormSearch />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <h5>Thiết lập cấu trúc mã sinh viên</h5>
          </Col>
          <Col span={24}>
            <StructCodeForm token={token} fireOnChange={this.handleReload} />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={reload}
              formSearch
              service={getStudents}
              paramSearch={paramSearchClass}
              columns={columns}
              allowGetData={!!paramSearchClass.courseId}
              rowKey="code"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <SetStudentCodePage />
  </Page>
);



