import React, { Component } from 'react';
import { Row, Col, Form, Button, Input, Checkbox, message } from 'antd';
import { updateStudentCode, deleteStudentCode } from 'services/StudentCode';
import { getQueryParams } from 'utils/urlUtil';

let timeOutInput;
@Form.create({ name: 'formScholarshipApproval' })
class StructCodeForm extends Component {
  constructor(props) {
    super(props);
    this.state = {};
    this.handleRemoveCode = this.handleRemoveCode.bind(this);
    this.createExpectedCode = this.createExpectedCode.bind(this);
  }

  handleCreateCode = e => {
    e.preventDefault();
    const { token } = this.props;
    this.props.form.validateFields((err, values) => {
      if(!err){
        const { codeLength, startOrder} = values;
        if (String(startOrder).length > codeLength) {
          message.warn('Số bắt đầu lớn hơn độ dài!');
          return;
        }
        this.props.form.setFieldsValue({
          codeCreateFrom: `${values.prefix}${this.calculateStartFrom(codeLength,startOrder)}${values.suffix}`,
          codeCreateTo: `${values.prefix}${this.calculateStartTo(codeLength)}${values.suffix}`,
        });
        const queryParams = getQueryParams();
        const paramsPost = {...queryParams,...values, token};
        updateStudentCode(
          paramsPost.trainTypeId,
          paramsPost.facultyId,
          paramsPost.courseId,
          paramsPost.studyFieldId,
          paramsPost.classId,
          paramsPost.codeLength,
          paramsPost.startOrder,
          paramsPost.prefix,
          paramsPost.suffix,
          paramsPost.withoutCode,
          paramsPost.token,
        ).then(() => this.props.fireOnChange());
      }
    });
  };

  handleRemoveCode = e => {
    e.preventDefault();
    const { token } = this.props;
    const queryParams = getQueryParams();
    const paramsPost = {...queryParams, token};
    deleteStudentCode(
      paramsPost.trainTypeId,
      paramsPost.facultyId,
      paramsPost.courseId,
      paramsPost.studyFieldId,
      paramsPost.classId,
      paramsPost.token,
    ).then(() => this.props.fireOnChange());
  };

  createExpectedCode = e => {
    e.preventDefault();
    clearTimeout(timeOutInput);
    timeOutInput = setTimeout(() => {
      this.props.form.validateFields((err, values) => {
        if(!err){
          const { codeLength, startOrder} = values;
          if (String(startOrder).length > codeLength) {
            message.warn('Số bắt đầu lớn hơn độ dài!');
            return;
          }
          this.props.form.setFieldsValue({
            codeCreateFrom: `${values.prefix}${this.calculateStartFrom(codeLength,startOrder)}${values.suffix}`,
            codeCreateTo: `${values.prefix}${this.calculateStartTo(codeLength)}${values.suffix}`,
          });
        } else {
          this.props.form.setFieldsValue({
            codeCreateFrom: '',
            codeCreateTo: '',
          });
        }
      });
    },800);
  };

  calculateStartFrom = (codeLength, startOrder) => {
    let minNum = '';
    for (let index = startOrder.length; index < codeLength; index += 1) {
      minNum += '0';
    }
    return `${minNum}${startOrder}`;
  }

  calculateStartTo = (codeLength) => {
    let maxNum = '';
    for (let index = 0; index < codeLength; index += 1) {
      maxNum += '9';
    }
    return maxNum;
  }

  render() {
    const { getFieldDecorator } = this.props.form;
    const styleForm = {
      display: 'flex',
      flexWrap: 'wrap',
      alignItems: 'flex-end',
    };
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleCreateCode}>
        <Row gutter={24} style={styleForm}>
          <Col span={6}>
            <Form.Item label="Độ dài của số tự tăng">
              {getFieldDecorator('codeLength', { 
                initialValue: '',
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập độ dài!',
                  },
                ],
              })(<Input onChange={this.createExpectedCode} />) }
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Bắt đầu đánh từ số">
              {getFieldDecorator('startOrder', { 
                initialValue: '',
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập số bắt đầu!',
                  },
                ],
              })(<Input onChange={this.createExpectedCode} />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Phần đầu của mã">
              {getFieldDecorator('prefix', { 
                initialValue: '',
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập phần đầu mã!',
                  },
                ],
              })(<Input onChange={this.createExpectedCode} />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Phần cuối của mã">
              {getFieldDecorator('suffix', { 
                initialValue: '',
              })(<Input onChange={this.createExpectedCode} onPaste={this.createExpectedCode} />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Giải mã sinh viên sẽ là">
              {getFieldDecorator('codeCreateFrom', { initialValue: '' })(<Input  />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Đến">
              {getFieldDecorator('codeCreateTo', { initialValue: '' })(<Input />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="">
              {/* <Checkbox>Sinh viên chưa có mã</Checkbox> */}
              {getFieldDecorator('withoutCode', {
                valuePropName: 'checked',
                initialValue: true,
              })(<Checkbox />)} Sinh viên chưa có mã
            </Form.Item>
          </Col>
          <Col span={6}>
            <Row>
              <Col span={12}>
                <Form.Item label="">
                  <Button type="primary" htmlType="submit">
                    Lập mã tự động
                  </Button>
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item label="">
                  <Button onClick={this.handleRemoveCode} type="danger">Xóa tất cả mã</Button>
                </Form.Item>
              </Col>
            </Row>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default StructCodeForm;



